* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  --normal-font: 400;
  --bold-font: 700;
  --bolder-font: 900;
  --bg-color: #fcfcfc;
  --primary-color: #4756df;
  --secondary-color: #ff7235;
  --primary-shadow: #8b8eaf;
  --secondary-shadow: #a17a69;
  --custom-color: #FBA223;
  --button-color: #023047;
  --bottom-margin: 0.5rem;
  --bottom-margin-2: 1rem;
  --line-height: 1.7rem;
  --transition: 0.3s;
}

html,
body {
  height: 100%;
  /* background-color: #f8f9fa; */
  scroll-behavior: smooth;
}

.main-container {
  display: flex;
  flex-direction: column;
  height: 100vh;
}

.main {
  flex: 1 1;
}

footer {
  background-color: #333;
  padding: 1rem;
  color: #fcfcfc;
  color: var(--bg-color);
}

a {
  cursor: pointer;
  text-decoration: none;
  color: #fcfcfc !important;
  color: var(--bg-color) !important;

}

p {
  text-align: justify;
}

.my-button {
  background-color: #FBA223;
  background-color: var(--custom-color);
  color: #fcfcfc;
  color: var(--bg-color);
  padding: 10px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 18px;
  cursor: pointer;
  border-radius: 8px;
  box-shadow: 0px 5px #f39819;
}

.socials {
  display: flex;
  flex-direction: column;
  position: fixed;
  right: 1%;
  bottom: 50%;
}

.socicon {
  width: 3rem;
  height: 3rem;
}

.scroll-up {
  position: fixed;
  right: 0.5%;
  bottom: 3%;
  cursor: pointer;
}

.up-arrow {
  width: 3rem;
  height: 3rem;
  border-radius: 50%;
}

.icons {
  height: 50px;
}
